import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    count: 0,
    user: {
      username: "",
      password: "",
      fullname: "",
      access_token: "",
      refresh_token: "",
      role: "",
      phone: ""
    },
    wepAPI: {
      url: "https://arcane-wildwood-74745.herokuapp.com/"
    }
  },
  mutations: {
    increment(state) {
      state.count++;
    }
  }
});



